.box {
  width: 5rem;
  height: 5rem;
  background-color: white;
  border: none;
  border-radius: 10%;
  box-shadow: 0px 0px 8px #888;
  text-align: center;
  font-size: 5rem;
  font-weight: bold;
  line-height: 5rem;
  margin: 0.5rem;
}

.x {
  color: rgb(255, 70, 37)
}

.o {
  color:rgb(44, 135, 255)
}

.box:hover {
  box-shadow: 0px 0px 15px #888;
}